<template>
  <div class="barChart">
    <div ref="barChart" class="bar_bda"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
let option = {
  tooltip: {
    trigger: "axis",
    axisPointer: {
      type: "shadow"
    },
    formatter: (params) => {
      // window.console.log(params);
      let html = [
        `<p style="padding-bottom: 4px; margin-bottom: 4px; border-bottom: 1px dashed #30bf9f;">`,
        `<strong>${params[0].name}</strong>`,
        `</p>`,
        `<p style="text-align: left;">`,
        `<span style="color: #30bf9f;">● </span>`,
        `数值：<strong>${params[0].value}</strong>`,
        `</p>`
      ]
      return html.join('');
    },
    // 优化Echarts的tooltip弹窗遮挡问题
    position: (point, params, dom, rect, size) => { // 鼠标坐标和提示框位置的参考坐标系是：以外层div的左上角那一点为原点，x轴向右，y轴向下
      // 提示框位置
      let x = 0; // x坐标位置
      let y = 0; // y坐标位置

      // 当前鼠标位置
      // eslint-disable-next-line no-irregular-whitespace
      let pointX = point[0];
      // eslint-disable-next-line no-irregular-whitespace
      let pointY = point[1];
      
      // 外层div大小
      // let viewWidth = size.viewSize[0];
      // let viewHeight = size.viewSize[1];
     
      // 提示框大小
      let boxWidth = size.contentSize[0];
      let boxHeight = size.contentSize[1];
     
      // boxWidth > pointX 说明鼠标左边放不下提示框
      // eslint-disable-next-line no-irregular-whitespace
      if (boxWidth > pointX) {
        x = pointX + 10;
      } else {  
        // 左边放的下
        x = pointX - boxWidth-10;
        // eslint-disable-next-line no-irregular-whitespace
      }
      
      // boxHeight > pointY 说明鼠标上边放不下提示框
      if (boxHeight > pointY) {
        y = 5;
      } else { // 上边放得下
        y = pointY - boxHeight;
      }
      return [x, y];
    }
  },
  grid: {
    width: "100%",
    left: "1%",
    top: "10",
    right: "0",
    bottom: "0"
  },
  xAxis: [
    {
      //左侧柱状图的X轴
      gridIndex: 0, //x 轴所在的 grid 的索引
      show: false
    }
  ],
  yAxis: [
    {
      //左侧柱状图的Y轴
      gridIndex: 0, //y轴所在的 grid 的索引
      splitLine: "none",
      axisTick: "none",
      axisLine: "none",
      axisLabel: {
        verticalAlign: "bottom",
        align: "left",
        padding: [20, 0, 5, 10],
        color: "#333",
        fontSize: "12"
      },
      data: [],
      inverse: true
    },
    {
      //左侧柱状图的Y轴
      gridIndex: 0, //y轴所在的 grid 的索引
      splitLine: "none",
      axisTick: "none",
      axisLine: "none",
      data: [],
      inverse: true,
      axisLabel: {
        show: true,
        verticalAlign: "bottom",
        align: "right",
        padding: [20, 20, 5, 10],
        color: "#333",
        fontSize: "12"
      }
    },
    {
      //左侧柱状图的Y轴
      gridIndex: 0, //y轴所在的 grid 的索引
      splitLine: "none",
      axisTick: "none",
      axisLine: "none",
      data: []
    }
  ],
  // yAxis:
  //   {
  //     type: "category",
  //     data: [],
  //     show: true,
  //     axisLine: {
  //       show: false
  //     },
  //     axisTick: {
  //       show: false
  //     },
  //     axisLabel: {
  //       verticalAlign: "bottom",
  //       align: "left",
  //       padding: [20, 0, 5, 10],
  //       textStyle: {
  //         color: "#333333",
  //         fontSize: "12"
  //       }
  //     },
  //     inverse: true
  //     // position: "left"
  //   },
  barWidth: "10",
  series: [
    {
      type: "bar",
      xAxisIndex: 0, //使用的 x 轴的 index，在单个图表实例中存在多个 x 轴的时候有用。
      yAxisIndex: 0,
      data: [260, 230, 290, 1022],
      itemStyle: {
        // color: "#73a0fa"

        barBorderRadius: [20, 20, 20, 20],
        color: new echarts.graphic.LinearGradient(
          0,
          0,
          1,
          0,
          [
            {
              offset: 0,
              color: "#12B190"
            },
            {
              offset: 1,
              color: "#99f9d6"
            }
          ],
          false
        )
      }
      // label: {
      //   show: true,
      //   position: "inside",
      //   color: "#fff",
      //   fontSize: 12
      // }
    }
  ]
};
export default {
  name: "barChart",
  components: {},
  props: {
    seriesDate: {
      type: Array,
      default: () => {
        return [];
      }
    },
    yAxisData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    color: {
      type: String,
      default: () => {
        return "";
      }
    }
  },
  watch: {
    seriesDate: {
      handler() {
        this.init();
        this.$forceUpdate();
      },
      deep: true
    }
  },
  data() {
    return {
      lineOption: option,
      myChart: null
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init(seriesDate = [], yAxisData = []) {
      if (this.$refs.barChart) {
        echarts.dispose(this.$refs.barChart);
      }
      this.lineOption.yAxis[0].data = yAxisData;
      this.lineOption.yAxis[1].data = seriesDate;
      this.lineOption.yAxis[1].axisLabel.color = this.color;
      this.lineOption.series[0].data = seriesDate;
      if (this.color === "#12B190") {
        this.lineOption.series[0].itemStyle.color = new echarts.graphic.LinearGradient(
          0,
          0,
          1,
          0,
          [
            {
              offset: 0,
              color: "#12B190"
            },
            {
              offset: 1,
              color: "#b9fde4"
            }
          ],
          false
        );
      } else {
        this.lineOption.series[0].itemStyle.color = new echarts.graphic.LinearGradient(
          0,
          0,
          1,
          0,
          [
            {
              offset: 0,
              color: "#CC8A00"
            },
            {
              offset: 1,
              color: "#FACE8A"
            }
          ],
          false
        );
      }
      // this.lineOption.series[0].itemStyle.color = this.color;
      this.mychart = echarts.init(this.$refs.barChart);
      this.mychart.setOption(this.lineOption);
      // let mychart = echarts.init(this.$refs.barChart, true); //监听窗口变化重新加载
      // window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载
    }
  }
};
</script>

<style scoped lang="scss">
.barChart {
  height: 100%;
  .bar_bda {
    height: 100%;
  }
}
</style>
